import React, { useContext } from "react"
import { PageLayout, PageTitle } from "../components"
import { Container, Image } from "react-bootstrap"
import { Link, graphql } from "gatsby"
import { ThemeContext, SEO } from "../utils"

export default ({ data }) => {

  const {
    author,
    occupation,
    designations,
    unemployed,
  } = data.site.siteMetadata
  const { toString } = useContext(ThemeContext)

  return (
    <PageLayout>
      <SEO title="About Me" />
      <PageTitle title="About Me" />
      <Container>
        {/*<Image
          rounded
          width="140"
          height="140"
          src={`../../icons/luke-${toString()}.png`}
          alt={author}
        />*/}
        <article className="w-75 m-auto pt-2 text-justify">
          <p className="text-center">
            {designations.map((attr, i) => (
              <span key={attr}>
                &nbsp;<b>{attr}</b>&nbsp;
                {i < designations.length - 1 && <>||</>}
              </span>
            ))}
          </p>
          <p className="i-5 mt-4 pt-2">
            Hello! My name is <b>{`${author}`}</b>.
            I have been interested in computer science since 2014 when I took a computer science 
            class in 10th grade. Since then, I have attended Ontario Tech University to study more 
            about computer science. I recently graduated and received a bachelor's degree in 
            computer science. Now, I would like to work professionally in the field to apply my 
            skills and gain more experience.
          </p>
          <p className="i-5 mt-4 pt-2">
            My academic achievements from university include making the Dean’s List for 1 term and 
            making the President’s List for the rest of the 7 terms. While studying at 
            Ontario Tech University, I have made several projects. Some of these projects can be 
            found in the projects page on this website. 
          </p>   
        </article>
        <article className="w-75 m-auto">
          {unemployed && (
            <>
              <hr />
              <p className="unemployed">
                <small>
                  I am <b>currently looking for new opportunities</b>! Please contact me at &nbsp;
                  <a
                    href="mailto:anushanvimalathasan@gmail.com"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    anushanvimalathasan@gmail.com
                  </a>
                  !
                </small>
              </p>
            </>
          )}
          <hr />
        </article>
      </Container>
    </PageLayout>
  )
}

export const query = graphql`
  query {
    site {
      siteMetadata {
        unemployed
        occupation
        author
        designations
      }
    }
  }
`
